.elements__list {
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    list-style-type: none;
    row-gap: 20px;
    column-gap: 17px;
}

@media screen and (max-width: 900px) {
    .elements__list {
        grid-template-columns: 1fr;
    }
} 