.header__row {
    max-width: 880px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-grow: 1;
}

@media screen and (max-width: 900px) {
    .header__row {
        width: 100%;
        margin: 0;
        gap: 130px;
    }
}
