.tooltip__container {
    position: relative;
    margin: auto;
    max-width: 75vw;
    max-height: 75vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: default;
    background-color: #FFFFFF;
    border-radius: 10px;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    z-index: 2;
}