.form__button {
    width: 358px;
    min-height: 50px;
    font-weight: 400;
    font-size: 18px;
    padding: 0;
    margin: 0;
    border-radius: 2px;
    cursor: pointer;
}

.form__button:hover {
    transition: .3s linear;
    opacity: 0.85;
}

@media screen and (max-width: 900px) {
    .form__button {
        width: 260px;
        min-height: 46px;
        font-size: 16px;
    }
}