.form__input {
    border: none;
    color: #FFFFFF;
    border-bottom: 2px solid rgba(204, 204, 204, 1);
    background: none;
    min-height: 27px;
    width: 358px;
    padding: 0;
}

.form__input::placeholder {
    color: #CCCCCC;
}

@media screen and (max-width: 900px) {
    .form__input {
        width: 260px;
    }
}