.header__menu {
    display: none;
    margin-right: 30px;
    margin-bottom: 27px;
}

.header__menu:hover {
    transition: .3s linear;
    opacity: .6;
}

@media screen and (max-width: 900px) {
    .header__menu {
        display: block;
        margin-bottom: 27px;
        cursor: pointer;
    }
}
