.header {
    padding-top: 45px;
    margin: 0 auto;
    max-width: 880px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgba(84, 84, 84, .7);
}

@media screen and (max-width: 900px) {
    .header {
        padding-top: 28px;
    }
} 